import { Component, Prop, State, Event, EventEmitter, Element, h, Watch } from '@stencil/core';

export type InputVerificationSize = 'sm' | 'md' | 'lg';
export type InputVerificationDigits = 4 | 6;

@Component({
  tag: 'bh-input-verification',
  styleUrl: 'bh-input-verification.css',
  shadow: true,
})
export class BhInputVerification {
  @Element() el!: HTMLElement;

  /**
   * Size variant of the input fields
   */
  @Prop() size: InputVerificationSize = 'md';

  /**
   * Number of digits (4 or 6)
   */
  @Prop() digits: InputVerificationDigits = 4;

  /**
   * Label text for the input
   */
  @Prop() label: string = 'Secure code';

  /**
   * Whether to show the label
   */
  @Prop() showLabel: boolean = true;

  /**
   * Hint text displayed below the inputs
   */
  @Prop() hintText: string = 'This is a hint text to help user.';

  /**
   * Whether to show the hint text
   */
  @Prop() showHintText: boolean = true;

  /**
   * Whether the input is in an error state
   */
  @Prop() error: boolean = false;

  /**
   * Whether the input is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Whether the field is required
   */
  @Prop() required: boolean = false;

  /**
   * Whether to show the separator for 6-digit codes
   */
  @Prop() showSeparator: boolean = true;

  /**
   * Placeholder character for empty inputs
   */
  @Prop() placeholder: string = '';

  /**
   * Width of the component
   */
  @Prop() width: string = '';

  /**
   * Current value of the verification code
   */
  @Prop({ mutable: true }) value: string = '';

  @State() values: string[] = [];
  @State() inputRefs: HTMLInputElement[] = [];

  /**
   * Event emitted when the value changes
   */
  @Event() bhChange!: EventEmitter<string>;

  /**
   * Event emitted when all digits are filled
   */
  @Event() bhComplete!: EventEmitter<string>;

  @Watch('value')
  watchValue(newValue: string) {
    const newValues = newValue.split('').concat(Array(this.digits).fill('')).slice(0, this.digits);
    this.values = newValues;
  }

  @Watch('digits')
  watchDigits() {
    this.values = Array(this.digits).fill('');
  }

  componentWillLoad() {
    this.values = this.value
      ? this.value.split('').concat(Array(this.digits).fill('')).slice(0, this.digits)
      : Array(this.digits).fill('');
  }

  componentDidLoad() {
    // Focus first input on mount if not disabled
    if (!this.disabled) {
      const firstInput = this.el.shadowRoot?.querySelector('input');
      firstInput?.focus();
    }
  }

  private updateValues(newValues: string[]) {
    this.values = newValues;
    const newValue = newValues.join('');
    this.value = newValue;
    this.bhChange.emit(newValue);

    // Check if all fields are filled
    if (newValues.every(val => val !== '')) {
      this.bhComplete.emit(newValue);
    }
  }

  private handleInputChange(index: number, event: Event) {
    const input = event.target as HTMLInputElement;
    const inputValue = input.value;

    // Only allow single digits
    const digit = inputValue.replace(/\D/g, '').slice(-1);

    const newValues = [...this.values];
    newValues[index] = digit;
    this.updateValues(newValues);

    // Auto-advance to next input if digit entered
    if (digit && index < this.digits - 1) {
      const nextInput = this.el.shadowRoot?.querySelectorAll('input')[index + 1];
      nextInput?.focus();
    }
  }

  private handleKeyDown(index: number, event: KeyboardEvent) {
    if (event.key === 'Backspace') {
      event.preventDefault();
      const newValues = [...this.values];

      if (this.values[index]) {
        // Clear current field
        newValues[index] = '';
        this.updateValues(newValues);
      } else if (index > 0) {
        // Move to previous field and clear it
        newValues[index - 1] = '';
        this.updateValues(newValues);
        const prevInput = this.el.shadowRoot?.querySelectorAll('input')[index - 1];
        prevInput?.focus();
      }
    } else if (event.key === 'ArrowLeft' && index > 0) {
      event.preventDefault();
      const prevInput = this.el.shadowRoot?.querySelectorAll('input')[index - 1];
      prevInput?.focus();
    } else if (event.key === 'ArrowRight' && index < this.digits - 1) {
      event.preventDefault();
      const nextInput = this.el.shadowRoot?.querySelectorAll('input')[index + 1];
      nextInput?.focus();
    } else if (event.key >= '0' && event.key <= '9') {
      event.preventDefault();
      const newValues = [...this.values];
      newValues[index] = event.key;
      this.updateValues(newValues);

      // Auto-advance to next input
      if (index < this.digits - 1) {
        const nextInput = this.el.shadowRoot?.querySelectorAll('input')[index + 1];
        nextInput?.focus();
      }
    }
  }

  private handlePaste(event: ClipboardEvent) {
    event.preventDefault();

    const pastedText = event.clipboardData?.getData('text') || '';
    const pastedDigits = pastedText.replace(/\D/g, '').slice(0, this.digits);

    if (pastedDigits) {
      const newValues = pastedDigits.split('').concat(Array(this.digits).fill('')).slice(0, this.digits);
      this.updateValues(newValues);

      // Focus on the next empty input or last input
      const nextEmptyIndex = newValues.findIndex(val => val === '');
      const focusIndex = nextEmptyIndex === -1 ? this.digits - 1 : Math.min(nextEmptyIndex, this.digits - 1);
      const inputToFocus = this.el.shadowRoot?.querySelectorAll('input')[focusIndex];
      inputToFocus?.focus();
    }
  }

  private handleFocus(event: Event) {
    const input = event.target as HTMLInputElement;
    input.select();
  }

  private renderInputs() {
    const inputs = [];

    for (let i = 0; i < this.digits; i++) {
      // Add separator for 6-digit codes
      if (this.digits === 6 && i === 3 && this.showSeparator) {
        inputs.push(
          <span key="separator" class={{ 'separator': true, [`size-${this.size}`]: true }}>
            -
          </span>
        );
      }

      inputs.push(
        <input
          key={i}
          type="text"
          inputmode="numeric"
          pattern="[0-9]"
          maxLength={1}
          value={this.values[i] || ''}
          placeholder={this.placeholder && i < this.placeholder.length ? this.placeholder[i] : ''}
          disabled={this.disabled}
          class={{
            'verification-input': true,
            [`size-${this.size}`]: true,
            'error': this.error,
          }}
          onInput={(e) => this.handleInputChange(i, e)}
          onKeyDown={(e) => this.handleKeyDown(i, e)}
          onPaste={(e) => this.handlePaste(e)}
          onFocus={(e) => this.handleFocus(e)}
          aria-label={`Digit ${i + 1} of ${this.digits}`}
        />
      );
    }

    return inputs;
  }

  render() {
    const wrapperStyle = this.width ? { width: this.width } : {};

    return (
      <div class="input-verification-wrapper" style={wrapperStyle}>
        <bh-label
          label={this.showLabel ? this.label : ''}
          required={this.required}
          disabled={this.disabled}
        >
          <div class="input-verification-container">
            {this.renderInputs()}
          </div>
        </bh-label>

        {this.showHintText && this.hintText && (
          <div class={{ 'hint-text': true, 'error': this.error }}>
            {this.hintText}
          </div>
        )}
      </div>
    );
  }
}
